<template>
  <div>
    <Modal
      v-model="modal1"
      title="查看广告配置（原生）">

      <div class="master-content">
        <div class="row"><label>广告配置名:</label>{{configName}}</div>
        <div class="row"><label>广告配置描述:</label>
          {{configDesc}}
        </div>

        <div class="row"><label>位置号:</label>{{positionNum}}</div>
        <div class="row"><label>所属代码位:</label>{{codeName}}</div>
        <div class="row"><label>终端类型:</label>{{appType}}</div>


      </div>
      <div slot="footer">
        <Button type="text" @click="modal1=false">返回</Button>
      </div>
    </Modal>
  </div>
</template>
<script>

  export default {
    data() {
      return {
        configName: "",
        configDesc: "",
        positionNum: "",
        appType: "",
        codeName: "",
        adConfigId: "",
        modal1: false,
      }
    },
    created() {

    },
    methods: {
      beforeLoad: function () {//还原所有属性
        this.configName = "";
        this.configDesc = "";
        this.positionNum = "";
        this.appType = "";
        this.codeName = "";
        this.adConfigId = "";
      },


      getAdConfigData: function (adConfigId) {
        this.adConfigId = adConfigId;
        let url = "/appConfig/getAppConfigById.json?appConfigId=" + this.adConfigId;
        this.$Http.get(url, {}).then(res => {
          let r = res.body.data;
          this.configName = r["configName"];
          this.configDesc = r["configDesc"];
          this.positionNum = r["positionNum"];
          this.appType = r["appType"] == 0 ? "ios" : "andriod";
          this.codeName = r["codeName"];
          this.modal1 = true;
        });
      }


    }

  }

</script>

<style scoped>
  .master-content {
    background: #fff;
    height: 100%;
    padding: 10px;
  }

  .master-content .row {
    padding: 10px;
    display: -webkit-flex; /* Safari */
    display: flex;
    align-items: center;

  }

  .master-content .row label {
    width: 100px;
  }

  .master-content .row.down {
    border-top: 1px solid #eaeaea;
    height: 100px;

  }


</style>



