<style>

.ivu-modal-wrap .ivu-modal-content .ivu-modal-body .ivu-modal-confirm {
    word-break: break-all;
}

</style> <style scoped> .ivu-page {
    text-align: right;
    margin-right: 30px;
    padding: 10px 10px;
}

.master-content-middle {
    display: -webkit-flex;
    /* Safari */
    display: flex;
    padding: 30px 10px;
    border: 1px solid #d7f0f2;
    align-items: center;
    margin-top: 10px;
}

.master-content-middle ul .master-content-middle-child {
    overflow: hidden;
    display: inline-block;
    margin-left: 20px;
}

.label-left {
    float: left;
    line-height: 32px;
    display: inline-block;
    width: 100px !important;
}

.topBtnWrap {
    display: flex;
    justify-content: space-between;
}

</style>

<template>

<div>
    <top-title title="广告配置（原生）"></top-title>
    <div style="padding:10px">
        <div>
            <div class="topBtnWrap">
                <div>
                    <Button class="btnColor" icon="plus" @click="showAdWindow">新建广告配置</Button>
                    <Button class="btnCancel" type="error" :disabled="deleteAdConfigParams.length?false:true" @click="deleteAll">删除</Button>
                    <Button type="ghost" :icon="sxIcon" @click="sx">筛选</Button>
                </div>
                <div>
                    <Input v-model="nameLike" placeholder="请输入广告配置名称" style="width: 300px"></Input>
                    <Button class="btnColor" @click="search">搜索</Button>
                </div>

            </div>


        </div>
        <div class="master-content-middle" v-show="isShow">
            <ul>
                <li class="master-content-middle-child">
                    <span class="label-left">所属代码位：</span>
                    <!-- <Select v-model="codeId" style="width:200px" filterable @on-change='selectCode' clearable>
                        <Option value="-99">全部</Option>
                        <Option v-for="(item,index) in codeList" :key="index" :value="item.fxAdCodeId">{{item.codeName}}</Option>
                        <Button @click="loadMore" type="text" long>{{textLoadMore}}</Button>
                    </Select> -->
                    <div class="main-element">
                        <el-select style="width: 200px;" v-model="codeId" filterable placeholder="请选择" @change="selectCode" clearable>
                            <el-option :value="-99" :label="`全部`"></el-option>
                            <el-option v-for="(item,index) in codeList" :key="index" :label="item.codeName" :value="item.fxAdCodeId">
                            </el-option>
                        </el-select>
                    </div>
                </li>
                <li class="master-content-middle-child">
                    <span class="label-left">终端类型：</span>
                    <!-- <Select v-model="appType" style="width:200px" filterable @on-change='selectChange' clearable>
                        <Option value="-99">全部</Option>
                        <Option v-for="(item,index) in $Constants.appTypeSelector" :key="index" :value="item.key">
                            {{ item.value}}
                        </Option>
                    </Select> -->
                    <div class="main-element">
                        <el-select style="width: 200px;" v-model="appType" filterable placeholder="请选择" @change="selectChange" clearable>
                            <el-option :value="-99" :label="`全部`"></el-option>
                            <el-option v-for="(item,index) in $Constants.appTypeSelector" :key="index" :label="item.value" :value="item.key">
                            </el-option>
                        </el-select>
                    </div>
                </li>

            </ul>

        </div>
        <Table style="margin-top:10px" :loading="loading" :border='true' :columns="columns" :data="data" @on-select="" @on-select-cancel="" @on-select-all="" @on-selection-change="selectData"></Table>
        <Page :current="pageNo" @on-change='clickPage' :total="totalCount" show-sizer show-total @on-page-size-change="changePageSize"></Page>

    </div>
    <show-ad-config ref="showAdConfig"></show-ad-config>
    <update-ad-config ref="updateAdConfig" v-on:filterParent="search"></update-ad-config>
    <create-ad-config ref="createAdConfig" v-on:filterParent="search"></create-ad-config>
</div>

</template>

<script>

import topTitle from '@/components/common/topTitle'
import createAdConfig from './child/createAdConfig'
import showAdConfig from './child/showAdConfig'
import updateAdConfig from './child/updateAdConfig'

export default {
    data() {
            return {
                tenplate: null,
                loading: false,
                disabled: true,
                sxIcon: 'chevron-down',
                isNative: '',
                nameLike: "",
                codeId: -99,
                appType: -99,
                pageSize: 10,
                pageNo: 1,
                isShow: false,
                totalCount: 0,
                codeList: [],
                data: [],
                deleteAdConfigParams: [],
                textLoadMore: "加载更多",
                codeListTotalPages: 0,
                codeListPageNo: 1,
                columns: [{

                    type: 'selection',
                    width: 60,
                    align: 'center'
                }, {
                    title: '广告配置ID',
                    key: 'adConfigId',
                }, {
                    title: '广告配置名',
                    key: 'adConfigName',

                }, {
                    title: '位置号',
                    key: 'positionNum',
                    width: 100
                }, {
                    title: '广告配置描述',
                    key: 'adConfigDesc',
                }, {
                    title: '所属代码位',
                    key: 'code',
                }, {
                    title: '终端类型',
                    key: 'type',
                }, {
                    title: '操作',
                    key: 'operation',
                    width: 200,
                    align: 'center',
                    render: (h, params) => {
                        return h('div'), [

                            h('Button', {
                                props: {
                                    type: 'primary',

                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px',
                                    backgroundColor: '#0676c4',
                                    borderColor: '#0676c4'
                                },
                                on: {
                                    click: () => {
                                        this.$refs.showAdConfig.beforeLoad();
                                        this.$refs.showAdConfig.getAdConfigData(params.row.adConfigId);
                                    }
                                }
                            }, '查看'),
                            h('Button', {
                                props: {
                                    type: 'info',

                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px',
                                    backgroundColor: '#1fa2fd',
                                    borderColor: '#1fa2fd'
                                },
                                on: {
                                    click: () => {
                                        this.$refs.updateAdConfig.beforeLoad();
                                        this.$refs.updateAdConfig.getAdConfigData(params.row.adConfigId);
                                    }
                                }
                            }, '修改'),
                            h('Button', {
                                props: {
                                    type: 'error',

                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px',
                                    backgroundColor: '#ea0b00',
                                    borderColor: '#ea0b00'
                                },
                                on: {
                                    click: () => {
                                        let v = [];
                                        v.push(params.row);

                                        this.deleteBox(v);
                                    }
                                }
                            }, '删除'),

                        ]
                    }
                }, ]
            }
        },
        created() {
            this.initTabList();
            this.getCodeList();
        },
        methods: {
            sx() {
                    this.isShow = !this.isShow;
                    if (this.isShow) {
                        this.sxIcon = "chevron-up"
                    } else {
                        this.sxIcon = "chevron-down"
                    }
                },
                initTabList() {
                    const url = `/appConfig/getAppConfigList.json?codeId=${this.codeId}&appType=${this.appType}&pageSize=${this.pageSize}&pageNo=${this.pageNo}&nameLike=${encodeURIComponent(this.nameLike)}`;
                    this.data = [];
                    this.$Http.get(url, {}).then(res => {
                        this.loading = false;
                        let arr = res.body.data.result;
                        let tabList = '';
                        this.totalCount = res.body.data.totalCount;
                        for (let key in arr) {
                            tabList = {
                                'adConfigId': arr[key].fxAdAppConfigId,
                                'adConfigName': arr[key].configName,
                                'adConfigDesc': arr[key].configDesc,
                                'code': arr[key].codeName,
                                'type': arr[key].appType == 0 ? "ios" : "andriod",
                                'positionNum': arr[key].positionNum
                            }
                            this.data.push(tabList);
                        }

                    })
                },
                selectCode(data) {
                    this.codeId = data;
                    if (this.codeId === undefined) {
                        this.codeId = -99;
                    }
                    //this.codeId = data ? data : -99;
                    this.pageSize = 10;
                    this.pageNo = 1;
                    this.initTabList();
                },
                clickPage(data) {
                    this.loading = true;
                    this.pageNo = data;
                    this.data = [];
                    this.initTabList();
                },
                changePageSize(pageSize) {
                    this.loading = true;
                    this.pageSize = pageSize;
                    this.pageNo = 1;
                    this.data = [];
                    this.initTabList();
                },
                search() {
                    // this.pageSize = 10;
                    // this.pageNo = 1;
                    this.data = [];
                    this.initTabList();
                },
                showAdWindow() {
                    this.$refs.createAdConfig.beforeLoad();
                    this.$refs.createAdConfig.showAdWindow();
                },
                selectChange(data) {
                    this.appType = data;
                    if (this.appType === undefined) {
                        this.appType = -99;
                    }
                    this.initTabList();
                },
                deleteBox(params) {
                    if (params.length == 0) return false;
                    let adConfigNames = params.map((item, index) => {
                        return item.adConfigName;
                    });
                    let adConfigIds = params.map((item, index) => {
                        return item.adConfigId;
                    });
                    this.$Modal.confirm({
                        title: '删除广告配置',
                        content: `你确定要删除` + adConfigNames.join(","),
                        onOk: () => {
                            this.deleteAdConfigData(adConfigIds.join(","));
                        }
                    })
                },
                deleteAdConfigData: function(adConfigIds) {
                    let url = "/appConfig/deleteAppConfig.json?" + "&appConfigId=" + adConfigIds;
                    this.$Http.get(url, {}).then(res => {

                        this.$Message.info('删除广告配置成功!');
                        this.initTabList();
                    });
                },
                getCodeList() {
                    const url = "/code/getCodeList.json?nameLike=&status=1&isNative=1&useStatus=&pageSize=10&pageNo=" + this.codeListPageNo + "&codeSize=&codeGroupId=";
                    this.$Http.get(url, {}).then(res => {
                        let arr = res.body.data.result
                            // this.codeList = arr;
                        for (var i = 0; i < res.body.data.result.length; i++) {
                            this.codeList.push(res.body.data.result[i])
                        }
                        this.codeListTotalPages = res.body.data.totalPages;
                    })
                },
                selectData: function(selection) {
                    this.deleteAdConfigParams = selection;
                    if (this.deleteAdConfigParams.length == 0) {
                        this.disabled = true;
                    } else {
                        this.disabled = false;
                    }
                },

                deleteAll: function() {
                    this.deleteBox(this.deleteAdConfigParams);
                    this.deleteAdConfigParams.length = 0;
                },

                loadMore() {
                    if (this.codeListPageNo == this.codeListTotalPages) {
                        this.textLoadMore = "已到最底部"
                        return;
                    } else {
                        this.codeListPageNo++;
                    }
                    this.getCodeList();
                },
        },
        components: {
            topTitle, showAdConfig, updateAdConfig, createAdConfig
        },
}

</script>



