<style>

* {
    list-style: none;
    padding: 0;
    margin: 0;
}

.newCode-child {
    margin-top: 20px;
}

.btn-wraps {
    margin-top: 100px;
}

.labelLeft {
    display: inline-block;
    width: 100px;
}

.label-right-active {
    color: red !important;
}

.label-right {
    display: inline-block;
    margin-left: 20px;
}

</style>

<template>

<div>
    <Modal v-model="modal" title="修改广告配置" :style="{width:'100%'}" class-name="modal-box">

        <div class="master-content">
            <div class="row">
                <label>广告配置名:</label>
                <Input v-model="configName" type="text" name="configName" placeholder="" @on-blur='userDataNum' @on-focus='onFocusName' style="width: 300px"></Input>
                <span :class="{'label-right-active':isAtive}" class="label-right">{{soltNameLen}}</span>
            </div>
            <div class="row">
                <label>广告配置描述:</label>
                <Input v-model="configDesc" type="text" name="configDesc" placeholder="" style="width: 300px"></Input>
            </div>

            <div class="row">
                <label>位置号:</label>
                <Input v-model="positionNum" type="text" name="positionNum" placeholder="" style="width: 300px"></Input>
            </div>
            <div class="row">
                <label>所属代码位:</label>
                <!-- <Select v-model="codeId" filterable style="width: 300px">
                    <Option v-for="item in codeList" :value="item.fxAdCodeId" :key="item.fxAdCodeId">{{ item.codeName}}
                    </Option>
                </Select> -->
                <div class="main-element">
                    <el-select style="width: 300px;" v-model="codeId" filterable placeholder="请选择">
                        <el-option v-for="(item,index) in codeList" :label="item.codeName" :value="item.fxAdCodeId" :key="item.fxAdCodeId"></el-option>
                    </el-select>
                </div>
            </div>
            <div class="row">
                <label>终端类型:</label>
                <!-- <Select v-model="appType" style="width:200px">
                    <Option v-for="(item,index) in $Constants.appTypeSelector" :key="index" :value="item.key">{{ item.value}}
                    </Option>
                </Select> -->
                <div class="main-element">
                    <el-select style="width: 200px;" v-model="appType" filterable placeholder="请选择">
                        <el-option v-for="(item,index) in $Constants.appTypeSelector" :label="item.value" :value="item.key" :key="index"></el-option>
                    </el-select>
                </div>
            </div>
        </div>
        <div slot="footer">
            <Button type="text" @click="modal=false">取消</Button>
            <Button class="btnColor" type="primary" @click="updateAdConfig">确定修改</Button>
        </div>
    </Modal>
</div>

</template>

<script>

export default {
    data() {
            return {
                configName: "",
                configDesc: "",
                positionNum: "",
                appType: "",
                codeName: "",
                codeId: "",
                adConfigId: "",
                isAtive: false,
                soltNameLen: '最多可输入100个字符',
                modal: false,
                codeList: [],
            }
        },
        created() {
            this.getCodeData();

        },

        methods: {
            beforeLoad: function() {
                this.configName = "";
                this.configDesc = "";
                this.positionNum = "";
                this.appType = "";
                this.codeName = "";
                this.codeId = "";
                this.adConfigId = "";
                this.soltNameLen = "最多可输入100个字符";
                this.isAtive = false;


            },
            cancel() {
                this.soltNameLen = "最多可输入100个字符";
                this.isAtive = false;
                this.modal = false;
            },
            getCodeData: function() {
                let url = "/code/getCodeList.json?status=1&isNative=1";
                this.$Http.get(url, {}).then(res => {

                    if (res.body.status == 0) {
                        let r = res.body.data.result;
                        console.log(r);
                        this.codeList = r;
                        console.log(this.codeList);
                    }
                });
            },
            getAdConfigData: function(adConfigId) {
                this.adConfigId = adConfigId;
                let url = "/appConfig/getAppConfigById.json?appConfigId=" + this.adConfigId;
                this.$Http.get(url, {}).then(res => {
                    if (res.body.status == 0) {
                        let r = res.body.data;
                        this.configName = r["configName"];
                        this.configDesc = r["configDesc"];
                        this.positionNum = r["positionNum"];
                        this.appType = r["appType"];
                        this.codeName = r["codeName"];
                        this.codeId = r["codeId"];
                        console.log("appType is " + this.appType);
                        this.modal = true;
                    }
                });
            },
            updateAdConfig: function() {
                let url = "/appConfig/updateAppConfig.json?configName=" + encodeURIComponent(this.configName) + "&configDesc=" + encodeURIComponent(this.configDesc) + "&positionNum=" + this.positionNum + "&appType=" + this.appType + "&codeId=" + this.codeId + "&appConfigId=" + this.adConfigId;
                this.$Http.get(url, {}).then(res => {
                    if (res.body.status == 0) {
                        this.modal = false;
                        this.$emit("filterParent");
                    } else {
                        this.modal = true;

                    }
                });

            },

            loadMore: function() {
                this.pageNo = this.pageNo + 1;
                this.getCodeData();
            },
            onFocusName() {
                this.soltNameLen = "最多可输入100个字符";
                this.isAtive = false;

            },
            userDataNum() {
                //姓名位数不在0-100间
                if (this.configName) {
                    var reg = /^[\s\S]{1,100}$/;
                    var r = this.configName.match(reg);
                    if (r == null) {
                        this.soltNameLen = "长度超过100个字符"
                        this.isAtive = true;
                    }
                } else {
                    this.soltNameLen = "物料名称不能为空"
                    this.isAtive = true;
                }
            }


        }

}

</script>



