<style>

* {
    list-style: none;
    padding: 0;
    margin: 0;
}

.newCode-child {
    margin-top: 20px;
}

.btn-wraps {
    margin-top: 100px;
}

.labelLeft {
    display: inline-block;
    width: 100px;
}

.label-right-active {
    color: red !important;
}

.label-right {
    display: inline-block;
    margin-left: 20px;
}

</style>
<style scoped>
 .row {
    overflow: hidden;
}

.row>label {
    float: left;
    display: inline-block;
    line-height: 32px;
    vertical-align: middle;
}

</style>

<template>

<div>
    <Modal v-model="modal" title="新建广告配置" style="{width:100%}" class-name="modal-box">

        <div class="master-content">
            <div class="row">
                <label>广告配置名:</label>
                <Input v-model="configName" type="text" name="configName" placeholder="请输入广告配置名" @on-blur='userDataNum' @on-focus='onFocusName' style="width: 300px"></Input>
                <span :class="{'label-right-active':isAtive}" class="label-right">{{soltNameLen}}</span>
            </div>
            <div class="row">
                <label>广告配置描述:</label>
                <Input v-model="configDesc" type="text" name="configDesc" placeholder="请输入广告配置描述" style="width: 300px"></Input>
            </div>

            <div class="row">
                <label>位置号:</label>
                <Input v-model="positionNum" type="text" name="positionNum" placeholder="请输入位置号" style="width: 300px"></Input>
            </div>
            <div class="row">
                <label>所属代码位:</label>
                <!-- <Select v-model="codeId" filterable style="width: 300px">
                    <Option v-for="(item,index) in codeList" :value="item.fxAdCodeId" :key="item.fxAdCodeId">{{item.codeName}}</Option>
                </Select> -->
                <div class="main-element">
                    <el-select style="width: 300px;" v-model="codeId" filterable placeholder="请选择">
                        <el-option v-for="(item,index) in codeList" :label="item.codeName" :value="item.fxAdCodeId" :key="index"></el-option>
                    </el-select>
                </div>
            </div>
            <div class="row">
                <label>终端类型:</label>
                <!-- <Select v-model="appType" style="width:200px">
                    <Option v-for="(item,index) in $Constants.appTypeSelector" :key="index" :value="item.key">{{item.value}}</Option>
                </Select> -->
                <div class="main-element">
                    <el-select style="width: 200px;" v-model="appType" filterable placeholder="请选择">
                        <el-option v-for="(item,index) in $Constants.appTypeSelector" :label="item.value" :value="item.key" :key="index"></el-option>
                    </el-select>
                </div>
            </div>
        </div>
        <div slot="footer">
            <Button type="text" @click="cancel">取消</Button>
            <Button class="btnColor" type="primary" @click="createAdConfig">新建</Button>
        </div>
    </Modal>
</div>

</template>

<script>

export default {
    data() {
            return {
                configName: "",
                configDesc: "",
                positionNum: "",
                appType: "",
                codeName: "",
                codeId: "",
                adConfigId: "",
                codeList: [],
                isAtive: false,
                soltNameLen: '最多可输入100个字符',
                modal: false,
            }
        },
        created() {
            this.getCodeData();
        },
        methods: {
            createAdConfig: function() {
                let url = "/appConfig/createAppConfig.json?configName=" + encodeURIComponent(this.configName) + "&configDesc=" + encodeURIComponent(this.configDesc) + "&positionNum=" + this.positionNum + "&appType=" + this.appType + "&codeId=" + this.codeId;
                this.$Http.get(url, {}).then(res => {
                    if (res.body.status == 0) {
                        this.modal = false;
                        this.$Message.success("新建广告配置成功");
                        this.$emit("filterParent");
                    } else {
                        this.modal = false;

                    }
                });

            },
            cancel() {
                this.soltNameLen = "最多可输入100个字符";
                this.isAtive = false;
                this.modal = false;
            },
            getCodeData: function() {
                let url = "/code/getCodeList.json?status=1&isNative=1";
                this.$Http.get(url, {}).then(res => {

                    if (res.body.status == 0) {
                        let r = res.body.data.result;
                        this.codeList = r;
                    }
                });
            },


            beforeLoad() {

                this.configName = '';
                this.configDesc = '';
                this.positionNum = '';
                this.codeId = '';
                this.appType = '';

            },

            showAdWindow: function() {
                this.modal = true;
            },
            onFocusName() {
                this.soltNameLen = "最多可输入100个字符";
                this.isAtive = false;

            },
            userDataNum() {
                //姓名位数不在0-100间
                if (this.customName != "") {
                    var reg = /^[\s\S]{1,100}$/;
                    var r = this.customName.match(reg);
                    if (r == null) {
                        this.soltNameLen = "长度超过100个字符"
                        this.isAtive = true;
                    }
                } else {
                    this.soltNameLen = "广告名称不能为空"
                    this.isAtive = true;
                }
            }

        }
}

</script>



